import React, { useEffect } from "react";
import { useSelector } from "react-redux";
import useBOSDropableArea from "../hooks/useBOSDropableArea";
import DragAndDropComponent from "../components/DragAndDrop/DragAndDrop";
import Loader from "../components/Loader/Loader";

export const withDroppableLogic = (WrappedComponent) => {
  return (props) => {
    const { initializeDroppableArea, loading } = useBOSDropableArea();

    // Use Redux for isDropped
    const isDropped = useSelector((state) => state.droppedObject.isDropped);

    // Check if a dynamic loadingObject value is passed via props;
    // if not, fallback to Redux.
    const loadingObject = props.objectDropped;

    console.warn('loadingObject...............', loadingObject)

    useEffect(() => {
      if (!isDropped) {
        initializeDroppableArea();
      }
    }, [isDropped, initializeDroppableArea]);

    // Show loading states
    if (loading || loadingObject) {
      return <Loader />;
    }

    // If not dropped, show drag-and-drop area
    if (!isDropped) {
      return <DragAndDropComponent />;
    }

    // Render the wrapped component when isDropped = true
    return <WrappedComponent {...props} />;
  };
};

